@font-face {
  font-family: "Open Sans";
  src: local("Open Sans Regular"), local("OpenSans-Regular"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page-body {
  overflow-x: hidden;
  display: grid;
  grid-template-areas: "header" "content" "footer";
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #ffffff;
  color: #444444;
  font-weight: 400;
  line-height: 1.6;
}
@media (min-width: 768px) {
  .page-body {
    font-size: 17px;
    line-height: 1.5;
  }
}

.page-body__header {
  position: relative;
  z-index: 2;
  grid-area: header;
  min-height: 56px;
}

.page-body__content {
  grid-area: content;
}

.page-body__footer {
  grid-area: footer;
}

.logo {
  display: block;
}
.logo:hover {
  opacity: 0.6;
}
.logo:active {
  opacity: 0.3;
}
.logo--active:hover {
  opacity: 1;
  cursor: auto;
}

.logo__pic {
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .logo__pic {
    height: auto;
    width: 140px;
  }
}
@media (min-width: 1150px) {
  .logo__pic {
    width: 149px;
  }
}

.toggle-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 27px;
  height: 14px;
  border: none;
  background-color: transparent;
  padding: 0;
}
.toggle-menu::before, .toggle-menu::after {
  width: 20px;
  height: 2px;
  background-color: #000000;
  content: "";
  transition: transform 0.2s linear 0s;
}
.toggle-menu::before {
  box-shadow: 0 6px 0 #000000;
  transition: box-shadow 0.2s linear 50ms;
}
.toggle-menu:hover {
  opacity: 0.6;
  cursor: pointer;
}
.toggle-menu:active {
  opacity: 0.3;
}
.toggle-menu--hide {
  display: none;
}
.toggle-menu--close::before {
  width: 100%;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  background-color: #000000;
  transform: translateY(6px) rotate(45deg);
}
.toggle-menu--close::after {
  width: 100%;
  background-color: #000000;
  transform: translateY(-6px) rotate(-45deg);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header__wrapper {
  outline: 2px dashed green;
  outline-offset: -2px;
}
.header__menu-wrapper {
  background-color: #999;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__menu-wrapper {
    display: flex;
  }
}
.header__toggle-menu {
  display: none;
}
@media (min-width: 768px) {
  .header__menu {
    display: flex;
  }
}
.header__callback-btn {
  display: block;
  text-decoration: none;
  color: #ffffff;
  padding-inline: 1.8666666667rem;
  padding-block: 0.8rem;
  background-color: #ff1b6f;
}
.menu {
  list-style: none;
  padding: 0;
}
@media (min-width: 768px) {
  .menu__item {
    padding-inline: 1.2rem;
    padding-block-start: 0.8rem;
    padding-block-end: 0.8rem;
    border-block-end: 0.2rem solid rgba(255, 27, 111, 0);
  }
}
@media (min-width: 768px) {
  .menu__item:hover {
    border-block-color: #ff1b6f;
  }
}
.menu__item-link {
  text-decoration: none;
  color: #ffffff;
}
.benefits {
  list-style: none;
  padding: 0;
}